
.col {
    flex: 1;
    width: 30%;
  }

  .skills__container{
    margin-left: 5rem;
  }
  
  .shape {
    /* margin: 25px auto 50px; */
    margin: 0.15rem;
    background-color: purple;
  }

  .skill_box{
    display: flex;
    flex-direction: row;
  }
  
  .shape-square {
    border-radius: 0.25rem;
    height: 1.2rem;
    width: 1.2rem;
  }

  .skill_table {
    border: 1rem;
    border-color: white;
    margin-left: 10.2rem;
    display: flex;
    flex-direction: row;
    margin-bottom: 2.5rem;
    
  }

  td{
    margin-left: 2rem;
    margin-right: 5rem;
  }


  /*===========FOR RESPONSIVENESS (SMALL DEVICES)==============*/

@media screen and (max-width: 700px) {
  .skill_table{
    margin-left: 6rem;
    margin-bottom: 1rem;
  }

  .skill_table table{
    margin-bottom: 1rem;
  }

  .skill_table td{
    width: 8rem;
  }

  .skill_table {
    display: flex;
    flex-direction: column;
  }
  .skills__container{
    margin-left: 0rem;
  }


}